import React from 'react';
import axios from 'axios';
import { PropTypes } from 'prop-types';

export default class PartnerLinkBffService {
  constructor(local, useToast) {
    this.local = local;
    this.useToast = useToast;
  }

  getApiUrl() {
    const { apiUrl } = window['runConfig'] || {
      apiUrl: 'https://access.stage.redhat.com/subscriptions/link/api/',
    };
    return apiUrl;
  }

  async getPartnerOrderDetailsAsync(partnerId, signal, callback) {

    return insights.chrome.auth.getToken().then((token) => {
      return axios
        .get(this.getApiUrl() + `${partnerId}/details`, {
          headers: { Authorization: `Bearer ${token}` },
          withCredentials: true, // required to supply cookies on cross-site requests
          signal: signal,
        })
        .then((res) => {
            // If it's a 200 response but the body says the request wasn't successful
            // call the error callback and make it look like an axios error, for compatibility
            if (typeof res?.data?.successful === "boolean" && !res.data.successful) {
                callback({response: res})
            } else {
                callback(null, res.data)
            }
        })
        .catch((error) => {
            callback(error)
        });
    });
  }

  async linkPartnerOrderToAccountAsync(partnerId, termsPdfId, callback) {
    console.debug(`Linking partner=${partnerId}, terms=${termsPdfId}`);

    return insights.chrome.auth.getToken().then((token) => {
      return axios
        .post(
          this.getApiUrl() + `${partnerId}/link`,
          {},
          {
            headers: { Authorization: `Bearer ${token}` },
            params: {
              termsPdfId: termsPdfId,
              linkAllOrders: true, // no longer optional
            },
            withCredentials: true, // required to supply cookies on cross-site requests
          }
        )
        .then((res) => callback(null, res.data.linkSuccessful))
        .catch((error) => callback(error));
    });
  }
}

PartnerLinkBffService.propTypes = {
  local: PropTypes.func.isRequired,
  useToast: PropTypes.func.isRequired,
};
